/* Floating button */

*{padding:0;margin:0;}



.float{
	position:fixed;
	width:60px;
	height:60px;
	bottom:40px;
	right:40px;
	background-color:darkmagenta;
	color:#FFF;
	border-radius:50px;
	text-align:center;
	box-shadow: 2px 2px 3px #999;
}

.my-float{
	margin-top:22px;
}

/* DropDown */

.dropbtn {
    padding-top: 8px;
    font-size: 20px;
    border: none;
    cursor: pointer;
  }
  
  .dropdown {
    float: right;
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    min-width: 160px;
    overflow: auto;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    right: 0;
    z-index: 1;
  }
  
  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  /* Comment Style */
  
  .comment-div{
    background-color: rgb(240, 227, 239);
    border-radius: 3%;
    margin-bottom: 5px;
    margin-right: 3%;
  }
  
  .comment-name{
      font-weight: 600;
      font-style: italic;
      
  }

  .comment-text{
      margin-left: 5px;
  }
  
  .comment-pic{
    vertical-align: middle;
    width: 30px;
    height: 30px;
    border-radius: 60%;
    padding-left: 3px;
    padding-top: 3px;
  }

  .img-parent{
      height:500px;
      width: 400px;
      border-radius: 5px;

  }
  .img-parent img{
      height: 100%;
      width: 100%;
  }

  .flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

